import React, { useState } from 'react';
import { isValidPin } from '../utils/pin-utils';
import './PinEntry.css';

export default function PinEntry({ onStart }) {
  const [pin, setPin] = useState('');

  const valid = isValidPin(pin);

  return (
    <div className="pin-entry">
      <h1 className="pin-title">Baby Monitor</h1>
      <p className="pin-subtitle">Enter the same PIN on both devices</p>

      <input
        className="pin-input"
        type="tel"
        inputMode="numeric"
        pattern="[0-9]*"
        maxLength={6}
        placeholder="PIN (4-6 digits)"
        value={pin}
        onChange={(e) => setPin(e.target.value.replace(/\D/g, ''))}
        autoFocus
      />

      <div className="pin-buttons">
        <button
          className="pin-btn pin-btn-baby"
          disabled={!valid}
          onClick={() => onStart(pin, 'baby')}
        >
          Baby
          <span className="pin-btn-desc">Camera &amp; mic near baby</span>
        </button>
        <button
          className="pin-btn pin-btn-parent"
          disabled={!valid}
          onClick={() => onStart(pin, 'parent')}
        >
          Parent
          <span className="pin-btn-desc">Watch &amp; listen</span>
        </button>
      </div>
    </div>
  );
}
