import { useEffect, useRef, useState, useCallback } from 'react';

const THRESHOLD = 0.25;
const SUSTAIN_MS = 2500;
const COOLDOWN_MS = 15000;

export default function useCryAlarm(level, enabled) {
  const [alarming, setAlarming] = useState(false);
  const loudSinceRef = useRef(null);
  const cooldownUntilRef = useRef(0);
  const alarmRef = useRef(null);

  const startAlarmSound = useCallback(() => {
    try {
      const ctx = new (window.AudioContext || window.webkitAudioContext)();
      const gainNode = ctx.createGain();
      gainNode.gain.value = 0.3;
      gainNode.connect(ctx.destination);

      const osc = ctx.createOscillator();
      osc.type = 'sine';
      osc.frequency.value = 800;
      osc.connect(gainNode);
      osc.start();

      // Alternate between 800Hz and 600Hz
      let high = true;
      const interval = setInterval(() => {
        high = !high;
        osc.frequency.setValueAtTime(high ? 800 : 600, ctx.currentTime);
      }, 500);

      alarmRef.current = { ctx, osc, interval };
    } catch (e) {
      // Audio playback blocked
    }
  }, []);

  const stopAlarmSound = useCallback(() => {
    if (alarmRef.current) {
      const { ctx, osc, interval } = alarmRef.current;
      clearInterval(interval);
      osc.stop();
      ctx.close().catch(() => {});
      alarmRef.current = null;
    }
  }, []);

  const showNotification = useCallback(() => {
    if ('Notification' in window && Notification.permission === 'granted') {
      try {
        new Notification('Baby Monitor', {
          body: 'Baby is crying!',
          tag: 'baby-cry',
          requireInteraction: true,
        });
      } catch (e) {
        // Notification blocked
      }
    }
  }, []);

  // Detect sustained loud audio
  useEffect(() => {
    if (!enabled || alarming) return;

    const now = Date.now();
    if (now < cooldownUntilRef.current) {
      loudSinceRef.current = null;
      return;
    }

    if (level >= THRESHOLD) {
      if (loudSinceRef.current === null) {
        loudSinceRef.current = now;
      } else if (now - loudSinceRef.current >= SUSTAIN_MS) {
        setAlarming(true);
        startAlarmSound();
        showNotification();
      }
    } else {
      loudSinceRef.current = null;
    }
  }, [level, enabled, alarming, startAlarmSound, showNotification]);

  const dismiss = useCallback(() => {
    stopAlarmSound();
    setAlarming(false);
    loudSinceRef.current = null;
    cooldownUntilRef.current = Date.now() + COOLDOWN_MS;
  }, [stopAlarmSound]);

  // Cleanup on unmount
  useEffect(() => {
    return () => stopAlarmSound();
  }, [stopAlarmSound]);

  return { alarming, dismiss };
}
