import { useEffect, useState, useRef, useCallback } from 'react';
import { getBabyPeerId } from '../utils/pin-utils';

function createDummyStream() {
  // Silent audio track
  const audioCtx = new AudioContext();
  const oscillator = audioCtx.createOscillator();
  const dest = audioCtx.createMediaStreamDestination();
  oscillator.connect(dest);
  oscillator.start();

  // Black video track via canvas
  const canvas = document.createElement('canvas');
  canvas.width = 320;
  canvas.height = 240;
  const ctx2d = canvas.getContext('2d');
  ctx2d.fillStyle = '#000000';
  ctx2d.fillRect(0, 0, 320, 240);
  const canvasStream = canvas.captureStream(15);

  // Combine both tracks into one stream
  const stream = new MediaStream();
  dest.stream.getAudioTracks().forEach((t) => stream.addTrack(t));
  canvasStream.getVideoTracks().forEach((t) => stream.addTrack(t));

  const sentInfo = `sent:V${stream.getVideoTracks().length}A${stream.getAudioTracks().length}`;

  return {
    stream,
    sentInfo,
    _canvas: canvas, // prevent garbage collection
    cleanup: () => {
      audioCtx.close().catch(() => {});
      canvasStream.getVideoTracks().forEach((t) => t.stop());
    },
  };
}

export default function useParentConnection(getPeer, peerStatus, pin) {
  const [remoteStream, setRemoteStream] = useState(null);
  const [connectionStatus, setConnectionStatus] = useState('calling');
  const [debugInfo, setDebugInfo] = useState('');
  const callRef = useRef(null);
  const cleanupRef = useRef(null);
  const canvasRef = useRef(null);
  const retryTimerRef = useRef(null);

  const call = useCallback(() => {
    const peer = getPeer();
    if (!peer || peer.destroyed) return;

    // Clean up previous call
    if (callRef.current) {
      callRef.current.close();
      callRef.current = null;
    }
    if (cleanupRef.current) {
      cleanupRef.current();
      cleanupRef.current = null;
    }

    setConnectionStatus('calling');

    const { stream: dummyStream, sentInfo, _canvas, cleanup } = createDummyStream();
    cleanupRef.current = cleanup;
    canvasRef.current = _canvas; // prevent GC

    setDebugInfo(sentInfo);

    const babyPeerId = getBabyPeerId(pin);
    const mediaConnection = peer.call(babyPeerId, dummyStream);

    if (!mediaConnection) {
      setConnectionStatus('calling');
      cleanup();
      cleanupRef.current = null;
      return;
    }

    callRef.current = mediaConnection;

    mediaConnection.on('stream', (s) => {
      const vi = s.getVideoTracks().length;
      const ai = s.getAudioTracks().length;
      setDebugInfo(`${sentInfo} recv:V${vi}A${ai}`);
      setRemoteStream(s);
      setConnectionStatus('connected');
    });

    mediaConnection.on('close', () => {
      setRemoteStream(null);
      setConnectionStatus('calling');
      cleanup();
      cleanupRef.current = null;
    });

    mediaConnection.on('error', () => {
      setRemoteStream(null);
      setConnectionStatus('calling');
      cleanup();
      cleanupRef.current = null;
    });
  }, [getPeer, pin]);

  // Initial call when peer is ready
  useEffect(() => {
    if (peerStatus === 'open') {
      call();
    }
    return () => {
      if (callRef.current) {
        callRef.current.close();
        callRef.current = null;
      }
      if (cleanupRef.current) {
        cleanupRef.current();
        cleanupRef.current = null;
      }
    };
  }, [peerStatus, call]);

  // Auto-retry every 3s when not connected
  useEffect(() => {
    if (connectionStatus === 'calling') {
      retryTimerRef.current = setTimeout(call, 3000);
    }
    return () => {
      if (retryTimerRef.current) clearTimeout(retryTimerRef.current);
    };
  }, [connectionStatus, call]);

  const retry = useCallback(() => {
    call();
  }, [call]);

  return { remoteStream, connectionStatus, retry, debugInfo };
}
