import React from 'react';
import './StatusIndicator.css';

const STATUS_MAP = {
  connecting: { label: 'Connecting...', color: 'var(--warning)' },
  open: { label: 'Ready', color: 'var(--accent)' },
  waiting: { label: 'Waiting...', color: 'var(--warning)' },
  calling: { label: 'Calling...', color: 'var(--warning)' },
  connected: { label: 'Connected', color: 'var(--success)' },
  disconnected: { label: 'Disconnected', color: 'var(--danger)' },
  error: { label: 'Error', color: 'var(--danger)' },
  closed: { label: 'Closed', color: 'var(--danger)' },
};

export default function StatusIndicator({ status }) {
  const info = STATUS_MAP[status] || { label: status, color: 'var(--text-secondary)' };

  return (
    <div className="status-indicator">
      <span className="status-dot" style={{ backgroundColor: info.color }} />
      <span className="status-label">{info.label}</span>
    </div>
  );
}
