import { useEffect, useRef, useState } from 'react';

export default function useMediaStream(enabled, facingMode = 'environment') {
  const [stream, setStream] = useState(null);
  const [error, setError] = useState(null);
  const streamRef = useRef(null);

  useEffect(() => {
    if (!enabled) return;

    let cancelled = false;

    async function start() {
      try {
        // Get new stream FIRST, before stopping the old one.
        // On iOS Safari, stopping old tracks before getUserMedia
        // can cause the same camera to be returned again.
        const s = await navigator.mediaDevices.getUserMedia({
          video: { facingMode: { exact: facingMode }, width: { ideal: 640 }, height: { ideal: 480 } },
          audio: { echoCancellation: true, noiseSuppression: true, autoGainControl: true },
        });

        if (cancelled) {
          s.getTracks().forEach((t) => t.stop());
          return;
        }

        // Now stop the old stream
        if (streamRef.current) {
          streamRef.current.getTracks().forEach((t) => t.stop());
        }

        streamRef.current = s;
        setStream(s);
        setError(null);
      } catch {
        // exact facingMode failed - try without exact
        try {
          const s = await navigator.mediaDevices.getUserMedia({
            video: { facingMode, width: { ideal: 640 }, height: { ideal: 480 } },
            audio: { echoCancellation: true, noiseSuppression: true, autoGainControl: true },
          });

          if (cancelled) {
            s.getTracks().forEach((t) => t.stop());
            return;
          }

          if (streamRef.current) {
            streamRef.current.getTracks().forEach((t) => t.stop());
          }

          streamRef.current = s;
          setStream(s);
          setError(null);
        } catch (e2) {
          if (!cancelled) setError(e2);
        }
      }
    }

    start();

    return () => {
      cancelled = true;
      if (streamRef.current) {
        streamRef.current.getTracks().forEach((t) => t.stop());
        streamRef.current = null;
        setStream(null);
      }
    };
  }, [enabled, facingMode]);

  return { stream, error };
}
